<template>
  <div class="newsdetail">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/news' }">公告首页</el-breadcrumb-item>
            <el-breadcrumb-item>公告详情</el-breadcrumb-item>
          </el-breadcrumb>
        </span>
      </div>
      <div v-html="rawHtml"></div>
    </el-card>
  </div>
</template>

<script>
import content from '../static/newsDetail'
export default {
  name: 'NewsDetail',
  data () {
    return {
      rawHtml: ''
    }
  },
  created () {
    let lens = content.length
    console.log(lens)
    console.log(this.$store.state.news.index)
    for (let i = 0; i < lens; i++) {
      if (this.$store.state.news.index === i) {
        let detail = content[i].content
        this.rawHtml = `${detail}`
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.newsdetail {
  .card-title {
    height: 40px;
  }
  .indent {
    text-indent: 2.5em;
  }
}
</style>



